import React, { useState } from 'react';
import { Link, withRouter } from 'react-router-dom';
import cookie from 'react-cookies';
import MAP from './map';

function findMapName(guid) {
    console.log(MAP);
    console.log(guid);
    const result = MAP.find((map) => {
        return map.guid === guid;
    }) || { name: { fr_FR: "LOL" }, id: "lol" };
    console.log(result);
    return {
        name: result.name.fr_FR,
        tag: result.id,
    }
}


const axios = require('axios');

function Match({ match, history }) {
    const [game, setGame] = useState(null);
    const [idMatch] = useState(match.params.idMatch);
    const [currentTeam, setCurrentTeam] = useState('A');
    const [currentMap, setCurrentMap] = useState(0);

    if (game === null) {
        axios.get(`https://api.overwatchleague.com/match/${idMatch}`)
            .then(function (response) {
                setGame(response.data);
            })
            .catch(function (error) {
                console.log(error);
            });
        return (
            <div>
                <h2>MATCH</h2>
            </div>
        );
    } else {
        let Maps = [];
        const PlayerName = []

        game.competitors[0].players.map(({ player }) => {
            PlayerName[player.id] = player.name
        })
        game.competitors[1].players.map(({ player }) => {
            PlayerName[player.id] = player.name

        })


        Maps.push(<Map key={currentMap} idMatch={idMatch} game={game} map={game.games[currentMap]} PlayerName={PlayerName} currentTeam={currentTeam}></Map>);

        const Style = {
            HeaderTeam: {
                A: {
                    background: `linear-gradient(to right,
                        #${game.competitors[0].primaryColor},
                        #${game.competitors[0].primaryColor} 10px,
                        #${game.competitors[0].secondaryColor} 10px,
                        #${game.competitors[0].secondaryColor} 20px,
                        #${game.competitors[0].primaryColor} 20px,
                        #${game.competitors[0].primaryColor} 30px,
                        #${game.competitors[0].primaryColor}20,
                        #${game.competitors[0].secondaryColor}05
                        )`,
                    color: `#${game.competitors[0].secondaryColor}`,
                },
                B: {
                    background: `linear-gradient(to left,
                        #${game.competitors[1].primaryColor},
                        #${game.competitors[1].primaryColor} 10px,
                        #${game.competitors[1].secondaryColor} 10px,
                        #${game.competitors[1].secondaryColor} 20px,
                        #${game.competitors[1].primaryColor} 20px,
                        #${game.competitors[1].primaryColor} 30px,
                        #${game.competitors[1].primaryColor}20,
                        #${game.competitors[1].secondaryColor}05
                        )`,
                    color: `#${game.competitors[1].primaryColor}`,
                }
            },

        };
        let menuMap = [];
        // menuMap.push(
        //     <div key={'all'} style={{
        //         backgroundColor: '#00000020',
        //         borderBottom: '5px solid #000',
        //         borderRadius: '10px',
        //         margin: '0 2px',
        //         // backgroundSize: 'contain',
        //         padding: '5px 10px 5px 20px'
        //     }}>All </div>
        // )
        game.games.forEach((map, key) => {
            const infoMap = findMapName(map.attributes.mapGuid);

            menuMap.push(
                <div key={map.id} className="map" style={{
                    backgroundImage: `url('../imgs/maps/${infoMap.tag}')`,
                    backgroundColor: (map.points[0] > map.points[1]) ? `#${game.competitors[0].primaryColor}20` : (map.points[0] === map.points[1]) ? `#00000020` : `#${game.competitors[1].primaryColor}20`,
                    borderBottom: (map.points[0] > map.points[1]) ? `5px solid #${game.competitors[0].primaryColor}` : (map.points[0] === map.points[1]) ? '5px solid #000' : `5px solid #${game.competitors[1].primaryColor}`,
                    backgroundRepeat: 'no-repeat',
                    backgroundPosition: 'right center',
                    borderRadius: '10px',
                    margin: '0 2px',
                    // backgroundSize: 'contain',
                    padding: '5px 10px 5px 20px',
                    cursor: 'pointer',
                }}
                    onMouseEnter={
                        () => {
                            setCurrentMap(key)
                        }
                    }
                >
                    <div className="name">
                        {infoMap.name}
                    </div>
                    <div className="winTag"
                        style={{
                            backgroundImage: (map.points[0] > map.points[1]) ? `url('../imgs/teams/${game.competitors[0].abbreviatedName}.png')` : (map.points[0] === map.points[1]) ? `#000000` : `url('../imgs/teams/${game.competitors[1].abbreviatedName}.png')`,
                            backgroundRepeat: 'no-repeat',
                            backgroundPosition: 'center center',
                            backgroundOrigin: 'padding-box',
                            backgroundSize: '40%',
                            border: (map.points[0] > map.points[1]) ? `2px solid #${game.competitors[0].primaryColor}` : (map.points[0] === map.points[1]) ? `2px solid #000000` : `2px solid #${game.competitors[1].primaryColor}`
                        }}
                    >

                    </div>
                    <div className="score">
                        {map.points.join(' - ')}
                    </div>

                </div>
            )
        })
        return (
            <div className="Game">
                <Link to={`/matches`} className="return">
                    {'Listes des Matchs'}
                </Link>

                <h2>{`${game.competitors[0].name} - ${game.competitors[1].name}`}</h2>
                <div className="body" >


                    <div className="choiceTeam">
                        <div className="teamA" style={Style.HeaderTeam.A}>
                            <div className="logo"
                                style={
                                    {
                                        backgroundImage: `url('../imgs/teams/${game.competitors[0].abbreviatedName}.png')`,
                                        backgroundRepeat: 'no-repeat',
                                        backgroundSize: 'contain',
                                        backgroundPosition: 'center center',
                                    }
                                }
                                onClick={
                                    () => {
                                        setCurrentTeam('A')
                                    }
                                }
                                onMouseEnter={
                                    () => {
                                        setCurrentTeam('A')
                                    }
                                }
                            ></div>
                        </div>
                        <div className="teamB" style={Style.HeaderTeam.B}>
                            <div className="logo"
                                style={
                                    {
                                        backgroundImage: `url('../imgs/teams/${game.competitors[1].abbreviatedName}.png')`,
                                        backgroundRepeat: 'no-repeat',
                                        backgroundSize: 'contain',
                                        backgroundPosition: 'center center',
                                    }
                                }
                                onMouseEnter={
                                    () => {
                                        setCurrentTeam('B')
                                    }
                                }
                            ></div>
                        </div>
                        <div className="menuMap flex">
                            {menuMap}
                        </div>
                        {Maps}
                    </div>



                    {/*  */}

                </div>


            </div>

        )
    }
}



function StatTeam({ Roster, currentTeam }) {
    if (Roster !== null) {
        Roster.sort((a, b) => {
            return b.kill - a.kill;
        })
        const divRosters = [];
        divRosters.push(<div className="flex" key={'header'}>
            <div className="name">pseudo</div>
            <div className="dmg">dmg</div>
            <div className="heal">heal</div>
            <div className="kill">kill</div>
            <div className="death">death</div>
        </div>)
        Roster.forEach((player, key) => {
            if (currentTeam === player.team) {
                divRosters.push(<div className="flex" key={key}>
                    <div className="name">{player.name}</div>
                    <div className="dmg">{player.dmg}</div>
                    <div className="heal">{player.heal}</div>
                    <div className="kill">{player.kill}</div>
                    <div className="death">{player.death}</div>
                </div>)
            }

        })
        // ()

        return (
            <div className="allStat">
                {divRosters}
            </div>
        )
    } else {
        return (
            <div></div>
        )
    }
}

function Map({ map, idMatch, PlayerName, currentTeam, game }) {
    const infoMap = findMapName(map.attributes.mapGuid)
    const number = map.number
    const [stat, setStat] = useState(null);
    const color = {
        0: {
            primary: game.competitors[0].primaryColor,
            secondary: game.competitors[0].secondaryColor,
        },
        1: {
            primary: game.competitors[1].primaryColor,
            secondary: game.competitors[1].secondaryColor,
        }
    }

    if (stat === null) {
        axios.get(`https://api.overwatchleague.com/stats/matches/${idMatch}/maps/${number}`)
            .then(function (response) {
                setStat(response.data);
            })
            .catch(function (error) {
                console.log(error);
            });
        return ''
    } else {

        if (currentTeam === "A") {
            return (
                <div className="Stat">
                    <div className="TeamA">
                        <StatMap stat={stat.teams[0]} PlayerName={PlayerName} infoMap={infoMap} color={color[0]}></StatMap>
                    </div>
                </div>
            )
        }
        else {
            return (
                <div className="Stat">
                    <div className="TeamB">
                        <StatMap stat={stat.teams[1]} PlayerName={PlayerName} infoMap={infoMap} color={color[1]}></StatMap>
                    </div>
                </div>
            )
        }

    }


}

function StatMap({ stat, PlayerName, infoMap, color }) {
    let Players = [];
    Players.push(<div className="Players flex"
        style={{ borderBottom: `5px solid #${color.secondary}` }}
        key={9999}>
        <div className="map">{infoMap.name}</div>
    </div>)
    Players.push(<div className="Players flex"
        style={{ borderBottom: `1px solid #${color.secondary}` }}
        key={0}>
        <div className="name">Pseudo</div>
        <div className="Heroes">Hero</div>
        <div className="dmg">DMG</div>
        <div className="heal">HEAL</div>
        <div className="kill">KILL</div>
        <div className="death">DEATH</div>
    </div>)
    stat.players.map((player, key) => {
        let Heroes = [];
        player.heroes.map((heroe) => {
            Heroes.push(<div className={`ohi ${heroe.name}`} key={heroe.name}></div>)
        })
        Players.push(
            <div className="Players flex"
                style={{ borderBottom: `1px solid #${color.secondary}` }}
                key={player.esports_player_id}
            >
                <div className="name">{PlayerName[player.esports_player_id]} </div>
                <div className="Heroes">
                    {Heroes}
                </div>
                <div className="dmg">{Math.floor((player.stats.find((stat) => { return stat.name === "damage" })) ? player.stats.find((stat) => { return stat.name === "damage" }).value : 0)}</div>
                <div className="heal">{Math.floor((player.stats.find((stat) => { return stat.name === "healing" })) ? player.stats.find((stat) => { return stat.name === "healing" }).value : 0)}</div>
                <div className="kill">{Math.floor((player.stats.find((stat) => { return stat.name === "eliminations" })) ? player.stats.find((stat) => { return stat.name === "eliminations" }).value : 0)}</div>
                <div className="death">{Math.floor((player.stats.find((stat) => { return stat.name === "deaths" })) ? player.stats.find((stat) => { return stat.name === "deaths" }).value : 0)}</div>
            </div>)
    })

    return (<div className="StatMap">
        {Players}
    </div>)
}

export default withRouter(Match);