import React, { useRef, useState } from 'react';
import cookie from 'react-cookies';
import { Link } from 'react-router-dom';


const axios = require('axios');


function Match({ match, bets, history }) {

    const myBet = (bets && bets.hasOwnProperty(match.id)) ? bets[match.id] : null;

    let TeamA = match.competitors[0];
    let TeamB = match.competitors[1];
    if (TeamA === null && TeamB === null) {
        TeamA = {
            logo: '',
            abbreviatedName: '???',
            score: 0,
        };
        TeamB = {
            logo: '',
            abbreviatedName: '???',
            score: 0,
        };
    } else {
        TeamA.score = match.scores[0].value;
        TeamB.score = match.scores[1].value;

    }





    const inputScoreA = useRef();
    const inputScoreB = useRef();
    const startMatch = match.startDateTS || match.startDate;
    function timeConverter(UNIX_timestamp) {
        var a = new Date(UNIX_timestamp);
        var months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        var year = a.getFullYear();
        var month = months[a.getMonth()];
        var date = a.getDate();

        var hour = (a.getHours() < 10) ? '0' + a.getHours() : a.getHours();
        var mins = (a.getMinutes() < 10) ? '0' + a.getMinutes() : a.getMinutes();
        var time = date + ' ' + month + ' ' + year + ' - ' + hour + ':' + mins;
        return time;
    }
    return (
        <div className="Match flex" onMouseLeave={
            () => {
                if (cookie.load('login') && bets) {
                    axios.post('../api/sendBet', {
                        scoreA: inputScoreA.current.value,
                        scoreB: inputScoreB.current.value,
                        idMatch: match.id,
                        user: cookie.load('user')
                    })
                        .then(function (response) {
                            cookie.save('bets', response.data, { path: '/' });
                        })
                        .catch(function (error) {
                            console.log(error);
                        });
                }


            }
        } >
            <div className="date" >
                {TeamA.score >= 1 || TeamB.score >= 1 ?
                    <Link to={`/match/${match.id}`}>
                        {timeConverter(startMatch)}
                    </Link>
                    : timeConverter(startMatch)}
            </div>
            <div className="TeamA flex">
                <img src={TeamA.logo} alt={TeamA.abbreviatedName} />
                <div className="tag">{TeamA.abbreviatedName}</div>
                <div className="score">
                    <div className="value">
                        {TeamA.score}
                    </div>
                    {cookie.load('login') && bets ?
                        <ModifScore inputScoreA={inputScoreA} inputScoreB={inputScoreB} match={match} team="A" bets={bets} >
                        </ModifScore>
                        : ''}
                </div>
            </div>
            <div className="separator">
            </div>
            <div className="TeamB flex">
                <img src={TeamB.logo} alt={TeamB.abbreviatedName} />
                <div className="score">
                    <div className="value">
                        {TeamB.score}
                    </div>
                    {cookie.load('login') && bets ?
                        <ModifScore inputScoreA={inputScoreA} inputScoreB={inputScoreB} match={match} team="B" bets={bets} >
                        </ModifScore>

                        : ''}
                </div>
                <div className="tag">{TeamB.abbreviatedName}</div>
            </div>
            {
                (myBet && myBet.hasOwnProperty('power')) ? 
                <div className="powerUse" style={
                    {
                        backgroundImage: `url('./imgs/powers/${myBet.power.type}.png')`,
                        backgroundSize: '80%',
                        backgroundRepeat: 'no-repeat',
                        backgroundPosition: 'center center',
                    }
                }>
                </div>
                : (startMatch > Date.now()) ?
                    (cookie.load('login') && cookie.load('user').power) ?
                        <Power user={cookie.load('user').login} match={match} />
                        : ''
                    : ''

            }




        </div>
    )

}

function Power({ user, match }) {
    const [powers, setPowers] = useState(null)
    const [toogle, setToogle] = useState(false);
    const tooltip = useRef(null);
    if (powers === null) {
        axios.post('api/getPowersUser', {
            user: user,
        })
            .then(function (response) {
                setPowers(response.data);
            })
            .catch(function (error) {
                console.log(error);
            });
        return (<div className="Power"
            onMouseEnter={() => { setToogle(true); }}
            onMouseLeave={() => { setToogle(false); setPowers(null); }}>
        </div>);

    } else {

        const liPower = Object.keys(powers).map((key) => {
            if (powers[key].date.use === 0) {
                return (
                    <div className="spell" key={key} style={{
                        backgroundImage: `url('./imgs/powers/${powers[key].type}.png')`,
                    }}
                        onMouseEnter={() => {
                            tooltip.current.innerHTML = powers[key].name;
                        }}
                        onMouseLeave={() => {
                            tooltip.current.innerHTML = 'Utiliser un "Bonus"';
                        }}
                        onClick={() => {
                            console.log('USE SPELL', key);
                            console.log('USE SPELL', match.id);
                            console.log('USE SPELL', user);
                            setPowers(null);
                            axios.post('api/usePower', {
                                user: user,
                                idMatch: match.id,
                                spell: key,
                            })
                                .then(function (response) {
                                    console.log(response.data);
                                    window.location.reload();
                                })
                                .catch(function (error) {
                                    console.log(error);
                                });


                        }}

                    >
                        {/* {powers[key].name} */}
                    </div>
                )
            }

        })

        return (
            <div className="Power"
                onMouseEnter={() => { setToogle(true); }}
                onMouseLeave={() => { setToogle(false); setPowers(null); }}>

                <div className={(!toogle) ? 'list hide' : 'list show'}>
                    <div className="title" ref={tooltip}>
                        Utiliser un "Bonus"
                    </div>
                    <div className="select" >

                        {liPower}
                    </div>
                </div>
            </div>
        )
    }



}




function ModifScore({ match, inputScoreA, inputScoreB, team, bets }) {

    const [today] = useState(Date.now());
    function changeCSS(input) {
        console.log(input);
        input.current.className = "scoreInput change";
        setTimeout(() => {
            input.current.className = "scoreInput";
        }, 500)

    }

    const [startMatch] = useState((Number.isInteger(match.startDate)) ? match.startDate : match.startDateTS)

    if (today - startMatch <= 0 || cookie.load('user').status == 9) {
        if (team === "A") {
            return (
                <div className="bet">

                    <input type="text" name="TeamA" className="scoreInput" ref={inputScoreA} disabled defaultValue={
                        (bets && bets.hasOwnProperty(match.id)) ?
                            bets[match.id].scoreA :
                            ''
                    } />
                    <div className="lilbtn" onClick={
                        () => {
                            changeCSS(inputScoreA);
                            const score = parseInt(inputScoreA.current.value);
                            (isNaN(parseInt(inputScoreB.current.value))) ? inputScoreB.current.value = 0 : inputScoreB.current.value = parseInt(inputScoreB.current.value);
                            (isNaN(score)) ? inputScoreA.current.value = 1 : (score + 1 > 4) ? inputScoreA.current.value = 4 : inputScoreA.current.value = score + 1;
                        }
                    }>+</div>

                    <div className="lilbtn" onClick={
                        () => {
                            changeCSS(inputScoreA);
                            const score = parseInt(inputScoreA.current.value);
                            (isNaN(parseInt(inputScoreB.current.value))) ? inputScoreB.current.value = 0 : inputScoreB.current.value = parseInt(inputScoreB.current.value);
                            (isNaN(score) || (score - 1) < 0) ? inputScoreA.current.value = 0 : inputScoreA.current.value = score - 1;
                        }
                    }>-</div>
                </div>
            )
        } else {
            return (
                <div className="bet">
                    <input type="text" name="TeamB" className="scoreInput" ref={inputScoreB} disabled defaultValue={
                        (bets && bets.hasOwnProperty(match.id)) ?
                            bets[match.id].scoreB :
                            ''
                    } />
                    <div className="lilbtn" onClick={
                        () => {
                            changeCSS(inputScoreB);
                            const score = parseInt(inputScoreB.current.value);
                            (isNaN(parseInt(inputScoreA.current.value))) ? inputScoreA.current.value = 0 : inputScoreA.current.value = parseInt(inputScoreA.current.value);
                            (isNaN(score)) ? inputScoreB.current.value = 1 : (score + 1 > 4) ? inputScoreB.current.value = 4 : inputScoreB.current.value = score + 1;
                        }
                    }>+</div>

                    <div className="lilbtn" onClick={
                        () => {
                            changeCSS(inputScoreB);
                            const score = parseInt(inputScoreB.current.value);
                            (isNaN(parseInt(inputScoreA.current.value))) ? inputScoreA.current.value = 0 : inputScoreA.current.value = parseInt(inputScoreA.current.value);
                            (isNaN(score) || (score - 1) < 0) ? inputScoreB.current.value = 0 : inputScoreB.current.value = score - 1;
                        }
                    }>-</div>
                </div>
            )
        }



    }
    else {

        if (team === "A") {
            return (
                <div className="bet">
                    <input type="text" name="TeamA" className="scoreInput" ref={inputScoreA} disabled defaultValue={
                        (bets && bets.hasOwnProperty(match.id)) ?
                            bets[match.id].scoreA :
                            ''
                    } />
                </div>)
        } else {
            return (
                <div className="bet">
                    <input type="text" name="TeamB" className="scoreInput" ref={inputScoreB} disabled defaultValue={
                        (bets && bets.hasOwnProperty(match.id)) ?
                            bets[match.id].scoreB :
                            ''
                    } />
                </div>)
        }

    }


}


export default Match;